import React from 'react'
import { Mutation } from 'react-apollo'
import gql from 'graphql-tag'

import { ALL_TODOS_QUERY } from './Todos'

import { Button } from 'rebass'

const DELETE_TODO_MUTATION = gql`
  mutation DELETE_TODO_MUTATION($id: ID!) {
    deleteTodo(id: $id) {
      id
    }
  }
`

function update(cache, payload) {
  console.log('manually update cache so client matches server')
  // console.log(ALL_TODOS_QUERY)
  const data = cache.readQuery({ query: ALL_TODOS_QUERY })
  console.log('data', data, 'payload', payload)
  return
}

const DeleteTodo = props => (
  <Mutation
    mutation={DELETE_TODO_MUTATION}
    variables={{ id: props.id }}
    update={update}
  >
    {(deleteTodo, { error }) => (
      <Button
        bg="black"
        color="white"
        onClick={() => {
          if (confirm(`Are you sure you want to delete this Todo`)) {
            deleteTodo().catch(err => {
              alert(err.message)
            })
            update()
          }
        }}
      >
        X
      </Button>
    )}
  </Mutation>
)

export default DeleteTodo
